@import '../shared';

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}
#root {
  width: 100%;
}
#form-anchor {
  display: block;
  padding-top: 100px;
  margin-top: -100px;
  position: relative;
  z-index: -1;
}
.order {
  p {
    text-align: left;
  }
}
.font-weight-bold {
  font-weight: bold;
}
.spinner-container {
  text-align: center;
  margin-bottom: 2rem;
  .spinner {
    color: $dark-teal;
  }
}
.customize {
  &__textarea {
    min-height: unset !important;
    margin: 0 auto;
    &-container {
      @include breakpoint($large) {
        padding: 0.5rem 1rem;
      }
    }
  }
  &__counter {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include breakpoint($large) {
      flex-direction: row;
    }
    .section__heading {
      @include breakpoint($large) {
        margin: 0 1rem;
      }
    }
  }
}
.form__header {
  margin-bottom: 1rem;
  .total {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    @include breakpoint($large) {
      margin: 0;
    }
    &__label {
      text-transform: uppercase;
      background: $offwhite;
      font-weight: bold;
      color: $medium-grey;
      padding: calc(0.9rem + 1px);
      @include breakpoint($large) {
        padding: calc(1rem + 1px);
      }
      &--border-right {
        border-right: 1px solid $medium-grey;
      }
      &--customize {
        display: none;
        @include breakpoint($medium) {
          display: inline-block;
        }
      }
    }
    &__price {
      border: 1px solid $medium-grey;
      font-weight: bold;
      padding: 0.9rem;
      @include breakpoint($large) {
        padding: 1rem;
      }
    }
  }
}
.plus-minus-input {
  align-items: center;
  width: 200px;
  margin: 0 1rem;
  .input-group-field {
    text-align: center;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    border: 1px solid $dark-teal;
    color: $orange;
    padding: 0.5rem 1rem;
    font-weight: bold;
    &::-webkit-inner-spin-button,
    ::-webkit-outer-spin-button {
      appearance: none;
    }
  }
  .input-group-button {
    .circle {
      border-radius: 50%;
      width: 40px;
      height: 40px;
      line-height: 40px;
      padding: 0;
      border-color: $dark-teal;
      background-color: $dark-teal;
      color: $white;
    }
  }
}
